
@import url('https://fonts.googleapis.com/css?family=Signika');
@import url('https://api.tiles.mapbox.com/mapbox.js/v1.3.1/mapbox.css');
@import url('https://netdna.bootstrapcdn.com/font-awesome/3.2.1/css/font-awesome.css');

html {
    font-family: 'Signika', sans-serif;
}
body{
  background: green;
  text-align: center;
}

#map { 
  position:absolute;
  top:0px; 
  bottom:0; 
  width:100%;
  height:2000px;
  background-image: url('https://s3-us-west-2.amazonaws.com/s.cdpn.io/52524/NewYork2_bg.png');
}
.notPlacemarked{
visibility:hidden;
}
.p{ color: #685206; font-family: 'Helvetica Neue', sans-serif; font-size: 20px; line-height: 24px; margin: 0 0 24px; text-align: justify; text-justify: inter-word; }
.input[type=text] {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.placemarked{
  //background: #FFC52F;
  //border-color:#ffffff;
  //border-width:5px;
  //border-style:solid;
  visibility:visible;
  height:55px;
  width:55px;
  //border-radius: 25px;
  position:relative;
  top:110px;
  //left:80px;
  margin-left:40%;
  z-index:100;
  transition: width 1s, height 1s, -webkit-transform 1s;
  -o-transition: width 1s, height 1s, transform 1s;
  transition: width 1s, height 1s, transform 1s;
  transition: width 1s, height 1s, transform 1s, -webkit-transform 1s;
-webkit-transition: width 1s, height 1s, -webkit-transform 1s;
}
.container {
    background: #00C993;
    position: absolute;
    display: block;
    width: 100%;
    //z-index:100;
    height: 2050px;
    color: white;
    text-align: center; 
    min-width: 24px;
}

.section{
position:absolute;
  display:block;
  width:100%;
  //top:400px;
  //margin: auto;
  //z-index:100;
  color:white;
  text-align: left; 
  //padding-left: 3em;
}

#sect1 { 
  top:400px;
}
#sect2 {
  top:1000px;
}
#sect3 {
  top:1500px;
}
#footer {
  top:2050px;
  //background:#DDD7D1;
  //margin:auto;
  height:50px;
  text-align:center;
}

#twitter{
  float:left;
  width: 20%;
  font-size:5em;
  padding-left:15%;
  //padding-right:10%;
}
#rocket{
  float:right;
  width: 20%;
  font-size:5em;
  padding-left:15%;
  //padding-right:10%;
}
#hiddenIcon{
  text-align:center;
  visibility:hidden;
  height:0;
  //float:none;
  //width: 100%;
  font-size:5em;
  //padding-left:15%;
  //padding-right:10%;
}

.anchor{
height:100px;
//margin-top:100px;
}

band2 h2{
text-align: left;
    
}
.explanation {
  min-width: 12.42em;
  float:left;
  //padding-top:5%;
  width:40%;
  //display:block;
}
.explanation-right {
  min-width: 12.42em;
  float:left;
  text-align:right;
  padding-left:5%;
  width:40%;
}

#phone {
    background: #DDD7D1;
    border-radius: 30px;
    -webkit-box-shadow: 0 0.5em 0 #BCB1A5;
            box-shadow: 0 0.5em 0 #BCB1A5;
    
    padding: 50px 18px 50px 18px;
    width: 20%;
    min-width:200px;
    max-width:400px;
    margin-right:5%;
    float:left;
}
#screen {
    background: #ffffff;
    height: 300px;
    overflow: hidden;
    padding: 0;
    position: relative;
    min-width:200px;
}

#topBar {
    background: #00C993;
    height: 10%;
    overflow: hidden;
    padding-left: 10px;
    padding-right: 10px;
    position: relative;
    min-width:100px;
}
#mc_embed_signup{
  text-align:right;
  background-color: #00C993;
}
#mce-EMAIL{
  text-align:right;
  width:auto;
  height:30px;  
  margin-bottom:20px;
  padding-right:1em;
  border-style:none;
  outline: none;
}
#mce-EMAIL:focus {
    outline: none;
  
}
#mc-embedded-subscribe{
  border-style:none;
}

.icon-bookmark{float:right;}
.icon-bookmark:hover {color: #FFC52F;}
.pressed{color: #FFC52F;}

h1, h3 {
    font-family: 'Signika', sans-serif;
    font-weight: 700;
    text-align: center;
}
.container h1 {
    font-size: 5em;
    line-height: 1em;
    /*margin-bottom: 1em;*/
}

a {
color: white;
}
.button {
    background: #DDD7D1;
    text-align: center;
    padding: .5em 1em;
    color: white;
    font-weight: bold;
    text-decoration: none;
    -webkit-box-shadow: 0 0.2em 0 #BCB1A5;
            box-shadow: 0 0.2em 0 #BCB1A5;
    text-transform: uppercase;
    letter-spacing: 0.1em;
    /*transition cross-browser stuff*/
    -webkit-transition: background 0.2s ease-out;  /* Safari 3.2+, Chrome */  /* Firefox 4-15 */
    -o-transition: background 0.2s ease-out;  /* Opera 10.5–12.00 */
    transition: background 0.2s ease-out;  /* Firefox 16+, Opera 12.50+ */
}
.button:hover {
    background: #BCB1A5;
}
a.arrow{
  color: white;
  text-align: center;
  font-size: 3em;
  //display:block;
  //width:100%;
  //float:none;
  text-decoration: none;
  padding-bottom:30px;
  /*transition cross-browser stuff*/
  -webkit-transition: color 0.2s ease-out;  /* Safari 3.2+, Chrome */  /* Firefox 4-15 */
  -o-transition: color 0.2s ease-out;  /* Opera 10.5–12.00 */
   transition: color 0.2s ease-out;  /* Firefox 16+, Opera 12.50+ */
}
a.arrow:hover {color: #FFC52F;}

@media only screen and (min-width: 767px) {
  //#phone {float:left;}
  body {
        font-size: 1.125em; /* 18px / 16px */
        
        //line-height: 1.7em;
    }
    .container {
        background-size: 100%;
    }
    .container h1 {
        font-size: 3.25em; /*52 / 16*/
    }
    a.button {
        padding: .75em 1.5em;
    }
}
@media only screen and (max-width: 550px) {
  .explanation{float:none;margin:auto; text-align:center;}
  .explanation-right{float:none;margin:auto; text-align:center;}
  #sect1{top:350px;}
  #phone{visibility:hidden; height: 0px; width:0px; padding:0;margin:0;}
  #map{width:0px;height:0px;}
  #sect2{top:900px;}
  //#twitter{float:none;margin:auto;padding-left:0em;}
  #rocket{visibility:hidden;}
  #twitter{visibility:hidden;padding-left:0em;}
  #hiddenIcon{visibility:visible;height:auto;}
  #mce-EMAIL{text-align:center;padding:0;float:none;}
  #mc_embed_signup{text-align:center;}
  
}



